.item {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: -1;
  
}
video {
  object-fit: cover;
  width: 100vw;
  height:600px;
  position: relative;
  top: 0;
  left: 0;
  z-index:-1 ;
  display: block;
}
.bg {
  object-fit: cover;
  width: 100vw;
  height:600px;
  position: relative;
  top: 0;
  left: 0;
  z-index:999999999 ;
  display: block;
}
.test{
  width: 200;
}
.inner { position: relative; height: auto; }
.full-height{ height: 100%; }

#video_overlays {
  position: absolute;

  z-index: 2;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
#video_box{position: relative;}

.content {
  position: relative;
width: 100%;
  margin: 0 auto;

}
div.sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  padding: 15px;
  background-color: #cae8ca;
  border: 2px solid #4CAF50;
}
.content video {
  width: 100%;
  display: block;
}
.content:before {
  content: '';
  position: absolute;

  border-radius: 5px;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

}
.content:after {
  content: '';
  position: absolute;

  border-radius: 5px;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

